'use client';

import { useEffect, useRef } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

if (typeof window !== 'undefined') {
  gsap.registerPlugin(ScrollTrigger);
}

export default function Logo() {
  const logoWhiteRef = useRef<HTMLDivElement>(null);
  const logoBlackRef = useRef<HTMLDivElement>(null);
  const logoRedRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (typeof window === 'undefined') return;

    const ctx = gsap.context(() => {
      // Create ScrollTrigger for each section
      // Section 1 (white background) - reveal black logo
      ScrollTrigger.create({
        trigger: '#section1',
        start: 'top bottom',
        end: 'bottom top',
        scrub: true,
        onUpdate: (self) => {
          const progress = self.progress;
          const trigger = self.trigger as HTMLElement;
          const triggerTop = trigger.getBoundingClientRect().top;
          const triggerBottom = trigger.getBoundingClientRect().bottom;
          const viewportHeight = window.innerHeight;
          const viewportCenter = viewportHeight / 2;

          if (logoBlackRef.current) {
            // Calculate clip position based on where section is relative to center
            let clipTop = 0;
            let clipBottom = 100;

            if (triggerTop > viewportCenter) {
              // Section is below center - no clipping
              clipTop = 100;
              clipBottom = 100;
            } else if (triggerBottom < viewportCenter) {
              // Section is above center - fully visible
              clipTop = 0;
              clipBottom = 0;
            } else {
              // Section is crossing center
              const sectionHeight = triggerBottom - triggerTop;
              const centerOffset = viewportCenter - triggerTop;
              const centerPercent = (centerOffset / sectionHeight) * 100;

              clipTop = 0;
              clipBottom = 100 - centerPercent;
            }

            logoBlackRef.current.style.clipPath = `inset(${clipTop}% 0 ${clipBottom}% 0)`;
          }
        },
      });

      // Section 2 (black background) - reveal red logo
      ScrollTrigger.create({
        trigger: '#section2',
        start: 'top bottom',
        end: 'bottom top',
        scrub: true,
        onUpdate: (self) => {
          const trigger = self.trigger as HTMLElement;
          const triggerTop = trigger.getBoundingClientRect().top;
          const triggerBottom = trigger.getBoundingClientRect().bottom;
          const viewportHeight = window.innerHeight;
          const viewportCenter = viewportHeight / 2;

          if (logoRedRef.current) {
            let clipTop = 0;
            let clipBottom = 100;

            if (triggerTop > viewportCenter) {
              clipTop = 100;
              clipBottom = 100;
            } else if (triggerBottom < viewportCenter) {
              clipTop = 0;
              clipBottom = 0;
            } else {
              const sectionHeight = triggerBottom - triggerTop;
              const centerOffset = viewportCenter - triggerTop;
              const centerPercent = (centerOffset / sectionHeight) * 100;

              clipTop = 0;
              clipBottom = 100 - centerPercent;
            }

            logoRedRef.current.style.clipPath = `inset(${clipTop}% 0 ${clipBottom}% 0)`;
          }
        },
      });

      // Section 3 (white background) - use black logo, manage with section 1
      ScrollTrigger.create({
        trigger: '#section3',
        start: 'top bottom',
        end: 'bottom top',
        scrub: true,
        onUpdate: (self) => {
          const trigger = self.trigger as HTMLElement;
          const triggerTop = trigger.getBoundingClientRect().top;
          const triggerBottom = trigger.getBoundingClientRect().bottom;
          const viewportHeight = window.innerHeight;
          const viewportCenter = viewportHeight / 2;

          if (logoBlackRef.current) {
            // Get current clip path
            const currentClip = logoBlackRef.current.style.clipPath;
            const match = currentClip.match(/inset\((\d+\.?\d*)% 0 (\d+\.?\d*)% 0\)/);

            let existingClipTop = match ? parseFloat(match[1]) : 0;
            let existingClipBottom = match ? parseFloat(match[2]) : 100;

            if (triggerTop > viewportCenter) {
              // Section 3 is below center - don't modify
            } else if (triggerBottom < viewportCenter) {
              // Section 3 is above center
              existingClipTop = 100 - ((triggerBottom / viewportHeight) * 100);
            } else {
              // Section 3 is crossing center from below
              const sectionHeight = triggerBottom - triggerTop;
              const centerOffset = viewportCenter - triggerTop;
              const centerPercent = (centerOffset / sectionHeight) * 100;

              existingClipTop = 0;
            }

            logoBlackRef.current.style.clipPath = `inset(${existingClipTop}% 0 ${existingClipBottom}% 0)`;
          }
        },
      });
    });

    return () => ctx.revert();
  }, []);

  return (
    <div className="fixed inset-0 flex flex-col justify-center items-center pointer-events-none z-[9999]">
      {/* Base White Logo (always visible on red background) */}
      <div
        ref={logoWhiteRef}
        className="absolute"
      >
        <div className="font-marker text-[clamp(180px,20vw,320px)] leading-[0.9] text-white jaffa-text-shadow-white font-bold tracking-tight">
          Jaffa
        </div>
        <div className="text-[clamp(24px,3vw,40px)] tracking-[12px] font-bold text-white bg-black px-6 py-3 mt-[-40px] border-4 border-white text-center">
          DIGITAL
        </div>
      </div>

      {/* Black Logo Layer (revealed on white sections) */}
      <div
        ref={logoBlackRef}
        className="absolute"
        style={{ clipPath: 'inset(100% 0 100% 0)' }}
      >
        <div className="font-marker text-[clamp(180px,20vw,320px)] leading-[0.9] text-black jaffa-text-shadow-black-red font-bold tracking-tight">
          Jaffa
        </div>
        <div className="text-[clamp(24px,3vw,40px)] tracking-[12px] font-bold text-black bg-white px-6 py-3 mt-[-40px] border-4 border-black text-center">
          DIGITAL
        </div>
      </div>

      {/* Red Logo Layer (revealed on black section) */}
      <div
        ref={logoRedRef}
        className="absolute"
        style={{ clipPath: 'inset(100% 0 100% 0)' }}
      >
        <div className="font-marker text-[clamp(180px,20vw,320px)] leading-[0.9] text-jaffa-red jaffa-text-shadow-red-black font-bold tracking-tight">
          Jaffa
        </div>
        <div className="text-[clamp(24px,3vw,40px)] tracking-[12px] font-bold text-jaffa-red bg-white px-6 py-3 mt-[-40px] border-4 border-jaffa-red text-center">
          DIGITAL
        </div>
      </div>
    </div>
  );
}
